@import url(https://rsms.me/inter/inter.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: #fff;
}

.ControlsIntroduction {
  position: fixed;
  width: 100%;
  height: 130px;
  bottom: 0;
  display: flex;
  flex-flow: column;
  align-items: center;
}

.KeyboardGFX {
  /* position: relative; */
  width: 61px;
  height: 40px;
}

.ControlsWrapper {
  position: relative;
}

.ControlsWrapper span {
  position: absolute;
  display: inline-block;
}

.ControlsTextForward {
  text-align: center;
  top: -16px;
  left: 0;
  width: 100%;
  transform: translate3d(0, -100%, 0);
}

.ControlsTextBackward {
  text-align: center;
  bottom: -60px;
  left: 0;
  width: 100%;
  transform: translate3d(0, -100%, 0);
}

.ControlsTextLeft {
  top: 40px;
  left: -50px;
  transform: translate3d(0, -100%, 0);
}

.ControlsTextRight {
  top: 40px;
  left: 80px;
  transform: translate3d(0, -100%, 0);
}

.KeyboardDescription {
  text-align: center;
  margin-top: 50px;
  max-width: 500px;
}

.Pages {
  position: fixed;
  z-index: 1;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
  color: black;
  font-feature-settings: "tnum";
  font-variant-numeric: tabular-nums;
  -webkit-font-smoothing: auto;
  -webkit-font-smoothing: antialiased;
  font-family: 'Inter var', sans-serif;
}

.skills-page-container {
  display: flex;
}

.about-page-container {
  display: flex;
  flex-direction: row-reverse;
}

.page {
  padding: 24px 12px 24px 48px;
  width: 50vw;
}

.jumbo {
  font-size: 3.4em;
  font-weight: 800;
  text-decoration: underline;
}

.jumbo-2 {
  font-size: 2.4em;
  font-weight: 400;
}

.featured-text {
  padding-left: 1.4em;
  font-size: 1em;
}

.trailsText {
  position: relative;
  width: 100%;
  color: black;
  /* font-weight: 800; */
  letter-spacing: -0.05em;
  will-change: transform, opacity;
}

.trailsText > div {
  overflow: hidden;
}

.container {
  /* display: flex;
  align-items: center;
  height: 100%;
  justify-content: center; */
}

.card-container {
  display: flex;
}

.card {
  width: 240px;
  min-height: 80px;
  margin: 0.4em;
  position: relative;
  background: linear-gradient(
    135deg,
    rgba(255, 255, 255, 0.55),
    rgba(255, 255, 255, 0.07)
  );
  /* border: 1px solid #212121; */
  padding: 0.8em 1.2em 0.8em 1.2em;
  border-radius: 10px;
  /* box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.082); */
  -webkit-backdrop-filter: blur(8px);
          backdrop-filter: blur(8px);
}
.card::before {
  position: absolute;
  content: '';
  inset: 0;
  z-index: -1;
  border-radius: 50px;
  background-size: contain;
  -webkit-filter: blur(20px);
  -moz-filter: blur(20px);
  -o-filter: blur(20px);
  -ms-filter: blur(20px);
}
.card .title {
  font-size: 1rem;
  /* line-height: 1.2; */
}
.card .text {
  font-size: 0.8rem;
  /* margin-top: 1em; */
}
.card .text a {
  text-decoration: none;
  color: inherit;
  font-weight: bold;
}
.card .text a:hover {
  text-decoration: underline;
}
.card .label {
  display: inline-block;
  padding: 0.1em;
  margin: 0 0.2em 0 0.2em;
  font-size: 0.8em;
  border: 1px solid #212121;
}

